.social {
  list-style: none;
  display: flex;
  justify-content: center;
}

.social__link {
  display: flex;
}

.social__link--vk {
  background: url("../img/icon-vk.svg") no-repeat 50% 50%;
  width: 22px;
  height: 21px;
  font-size: 0;
}

.social__link--ig {
  background: url("../img/icon-insta.svg") no-repeat 50% 50%;
  width: 17px;
  height: 20px;
  margin: 0 35px;
  font-size: 0;
}

.social__link--fb {
  background: url("../img/icon-fb.svg") no-repeat 50% 50%;
  width: 20px;
  height: 20px;
  font-size: 0;
}

@media (min-width: @tablet-px) {
  .social__link--vk {
    width: 32px;
    height: 17px;
  }

  .social__link--ig {
    width: 24px;
    height: 24px;
    margin: 0;
  }

  .social__link--fb {
    width: 15px;
    height: 29px;
  }

  .social {
    justify-content: space-between;
    width: 125px;
    margin-left: 15px;
    margin-bottom: 4px;
  }
}
